body {
  display : flex;
  flex-direction: column;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #01450b; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.profile_pic {
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  opacity: 0.8;
}

.image-mac {
  width: 13%;
}

.card_size {
  width: 25rem;
}
.text-padding {
  padding-right: 10%;
  padding-left: 10%;
}

p.padding {
  padding-right: 15rem;
}

div.center {
  margin: auto;
  text-align: center;
  padding-top: 15%;
}

.page-size {
  height: 100%;
}

.project-size {
  height: 50vh;
}

.contact-size {
  height: 50vh;
}

.size {
  height: 70vh;
}

.btn-primary-outline {
  background-color: transparent;
}